.Mars {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: blue;
  position: relative;
  background-image: url(./assets/grid.jpg);
  background-repeat: repeat;
  background-size: 350px 350px; }
  .Mars .bigCrater {
    background-image: url(./assets/pool.png);
    height: 100%;
    width: 100%;
    background-position: cover;
    background-size: 398px 698px;
    /* Full height */ }
  .Mars .smallCrater {
    background-image: url(./assets/crater.png);
    height: 100%;
    width: 100%;
    background-position: cover;
    background-size: 300px 300px; }
  .Mars .marsCover {
    background-color: rgba(0, 0, 0, 0.3); }
  .Mars .blueTileDiv {
    background-image: url(./assets/bluetile.jpg);
    height: 100%;
    width: 100%;
    background-repeat: repeat; }
  .Mars .blueChair {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(./assets/pool/yellowchair.png);
    background-size: 200px 200px; }
