.WaveForms {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .WaveForms .react-player {
    position: absolute;
    top: 0;
    margin: auto;
    z-index: -100;
    overflow: hidden; }
  .WaveForms .mainContent {
    /* made titlebar position relative to fit this under without padding */
    /* padding-top: 22px; */
    z-index: 10;
    pointer-events: none; }
  .WaveForms .waveCover {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 55, 55, 0.4); }

@keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }
  .WaveForms #defaultCanvas0 {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    pointer-events: none; }
  .WaveForms button {
    height: 100px;
    font-size: 50px; }
  .WaveForms .ears .ear {
    background: transparent; }
  .WaveForms .moon {
    height: 100%;
    width: 100%;
    background-image: url(./assets/shell.jpg);
    background-size: 120% 100%;
    background-position: -10px -15px;
    background-repeat: no-repeat; }
  .WaveForms .shell {
    width: 100%;
    height: 100%;
    background-image: url(https://media.giphy.com/media/l4FGo0SjgDIy67d9S/giphy-downsized.gif);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 90%; }
  .WaveForms .earCursor-0 {
    cursor: url(./assets/cursors/0.png), auto !important; }
  .WaveForms .earCursor-1 {
    cursor: url(./assets/cursors/1.png), auto !important; }
  .WaveForms .earCursor-2 {
    cursor: url(./assets/cursors/2.png), auto !important; }
  .WaveForms .earCursor-3 {
    cursor: url(./assets/cursors/3.png), auto !important; }
  .WaveForms .earCursor-4 {
    cursor: url(./assets/cursors/4.png), auto !important; }
  .WaveForms .earCursor-5 {
    cursor: url(./assets/cursors/0.png), auto !important; }
